import React, { useRef } from 'react';
import PullToRefresh from '../components/PullToRefresh';
import CommunityFund from '../components/CommunityFund';
import BDNHolders from '../components/BDNHolders';
import FundProfitChart from '../components/FundProfitChart';
import useWallet from '../hooks/useWallet';
import TopBar from '../components/TopBar';

export default function Home() {
  const { syncCurrentWallet, refreshBalance } = useWallet();
  const fundRef = useRef();
  const holdersRef = useRef();

  const handleRefresh = async () => {
    // 1️⃣ 同步当前浏览器钱包地址
    await syncCurrentWallet();
    await refreshBalance();

    // 2️⃣ 刷新基金和持有人数据
    if (fundRef.current?.refresh) await fundRef.current.refresh();
    if (holdersRef.current?.refresh) await holdersRef.current.refresh();
  };

  return (
    <PullToRefresh onRefresh={handleRefresh}>
        <TopBar/>
      <CommunityFund ref={fundRef} />
      <BDNHolders ref={holdersRef} />
      <FundProfitChart />
    </PullToRefresh>
  );
}
