import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

export default function PageTitle() {
  const { pathname } = useLocation();

  useEffect(() => {
    switch (pathname) {
      case '/':
        document.title = '首页 - STR社区';
        break;
      case '/community':
        document.title = '社区 - STR社区';
        break;
      case '/profile':
        document.title = '我的 - STR社区';
        break;
      default:
        document.title = 'STR社区';
    }
  }, [pathname]);

  return null;
}
