import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import { SERVER } from "../config/network"; // Web DApp 的 network 配置
import "chart.js/auto"; // Chart.js 必须

export default function FundProfitChart() {
  const API_URL = SERVER.baseUrl + "/api/fundTrend";

  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedYear, setSelectedYear] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch(API_URL);
        const json = await res.json();
        if (Array.isArray(json)) {
          setData(json);
          const yearsArray = Array.from(
            new Set(json.map((item) => item.date.split("-")[0]))
          );
          if (yearsArray.length > 0) setSelectedYear(yearsArray[yearsArray.length - 1]);
        } else {
          setData([]);
        }
      } catch (err) {
        console.error("FundTrend fetch error:", err);
        setData([]);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [API_URL]);

  if (loading) return <div style={{ margin: 20 }}>加载中...</div>;

  // 分组
  const groupedData = {};
  data.forEach((item) => {
    const year = item.date.split("-")[0];
    if (!groupedData[year]) groupedData[year] = [];
    groupedData[year].push(item);
  });

  const years = Object.keys(groupedData);
  const yearData = groupedData[selectedYear] || [];

  if (yearData.length === 0) return <div style={{ margin: 20 }}>暂无数据</div>;

  const labels = yearData.map((d) => d.date.split("-")[1]);
  const latestValues = yearData.map((d) => Number(d.latestValue) || 0);

  let previousProfitSum = 0;
  for (const year of years) {
    if (year === selectedYear) break;
    previousProfitSum += groupedData[year].reduce(
      (sum, item) => sum + (isNaN(item.profit) ? 0 : item.profit),
      0
    );
  }

  const profitCumulative = [];
  yearData.reduce((acc, item) => {
    const sum = acc + (isNaN(item.profit) ? 0 : item.profit);
    profitCumulative.push(sum);
    return sum;
  }, previousProfitSum);

  const chartData = {
    labels,
    datasets: [
      {
        label: "基金最新值",
        data: latestValues,
        borderColor: "#2196F3",
        backgroundColor: "rgba(33, 150, 243, 0.1)",
        tension: 0.3,
      },
      {
        label: "累计利润",
        data: profitCumulative,
        borderColor: "#FF5722",
        backgroundColor: "rgba(255, 87, 34, 0.1)",
        tension: 0.3,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: { position: "top" },
    },
    scales: {
      y: { beginAtZero: true },
    },
  };

  return (
    <div style={{ margin: "14px 0", background: "#fff", borderRadius: 8, padding: 12 }}>
      <h3 style={{ marginBottom: 12 }}>基金趋势</h3>
      <Line data={chartData} options={options} />
      {/* 年份选择 */}
      <div style={{ marginTop: 12, textAlign: "center" }}>
        {years.map((year) => (
          <button
            key={year}
            onClick={() => setSelectedYear(year)}
            style={{
              margin: "0 4px",
              padding: "4px 12px",
              borderRadius: 20,
              border: "none",
              cursor: "pointer",
              background: selectedYear === year ? "#2196F3" : "#e0e0e0",
              color: selectedYear === year ? "#fff" : "#000",
            }}
          >
            {year}
          </button>
        ))}
      </div>
    </div>
  );
}
