import { useState, useCallback, useEffect, useRef } from 'react';
import { ethers } from 'ethers';

export default function useWallet() {
  const [address, setAddress] = useState('');
  const [balance, setBalance] = useState('');
  const triedAutoConnect = useRef(false); // ✅ 防止重复弹窗

  const refreshBalance = useCallback(async (userAddress = address) => {
    if (!window.ethereum || !userAddress) return;
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    try {
      const balanceWei = await provider.getBalance(userAddress);
      setBalance(ethers.utils.formatEther(balanceWei));
    } catch (err) {
      console.error('刷新余额失败:', err);
    }
  }, [address]);

  const connectWallet = useCallback(async () => {
    if (!window.ethereum) {
      alert('请使用钱包浏览器访问（如 MetaMask、TP、Bitget 等）');
      return;
    }
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const accounts = await provider.send('eth_requestAccounts', []);
    if (accounts.length > 0) {
      setAddress(accounts[0]);
      refreshBalance(accounts[0]);
    }
  }, [refreshBalance]);

  const checkAuthorized = useCallback(async () => {
    if (!window.ethereum) return;
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const accounts = await provider.listAccounts();

    if (accounts.length > 0) {
      setAddress(accounts[0]);
      refreshBalance(accounts[0]);
    } else {
      setAddress('');
      setBalance('');

      // ✅ 如果还没弹过钱包授权，自动尝试一次
      if (!triedAutoConnect.current) {
        triedAutoConnect.current = true;
        try {
          const reqAccounts = await provider.send('eth_requestAccounts', []);
          if (reqAccounts.length > 0) {
            setAddress(reqAccounts[0]);
            refreshBalance(reqAccounts[0]);
          }
        } catch (e) {
          console.warn('用户拒绝授权或未安装钱包');
        }
      }
    }
  }, [refreshBalance]);

  useEffect(() => {
    checkAuthorized(); // ✅ 页面刷新时执行一次检测和自动授权

    if (!window.ethereum) return;

    const handleAccountsChanged = (accounts) => {
      if (accounts.length > 0) {
        setAddress(accounts[0]);
        refreshBalance(accounts[0]);
      } else {
        setAddress('');
        setBalance('');
      }
    };

    const handleChainChanged = () => refreshBalance();

    window.ethereum.on('accountsChanged', handleAccountsChanged);
    window.ethereum.on('chainChanged', handleChainChanged);

    return () => {
      window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
      window.ethereum.removeListener('chainChanged', handleChainChanged);
    };
  }, [checkAuthorized, refreshBalance]);

  return { address, balance, connectWallet, refreshBalance };
}
