
import { NavLink } from 'react-router-dom';

export default function BottomNav() {
  return (
    <div style={{
      display: 'flex',
      justifyContent: 'space-around',
      padding: 10,
      borderTop: '1px solid #ccc',
      position: 'fixed',
      bottom: 0,
      width: '100%',
      backgroundColor: '#fff'
    }}>
      <NavLink to="/" end style={({isActive}) => ({color: isActive ? '#2196F3' : '#000'})}>首页</NavLink>
      <NavLink to="/community" style={({isActive}) => ({color: isActive ? '#2196F3' : '#000'})}>社区</NavLink>
      <NavLink to="/profile" style={({isActive}) => ({color: isActive ? '#2196F3' : '#000'})}>我的</NavLink>
    </div>
  );
}
